// CertItem Component
import React, { lazy, Suspense} from 'react'
import './cert-item.scss'
import {Link} from 'react-router-dom'

const Img = lazy(() => import('../../../components/Img'))

const CertItem = ({ name, img, url, school }) => {
  return (
    <div className="cert">
      <Link to={`/training/${url}`} aria-label={school}>
        <Suspense>
          <Img path={img} alt={school} />
        </Suspense>
      </Link>
      <div className="cert-desc">
        <p>
          <span className="cert-title">Título</span>
          <span>{name}</span>
        </p>
        <p>
          <span className="cert-title">Centro</span>
          <span>{school}</span>
        </p>

      </div>
    </div>
  )
}

export default CertItem
